import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {SharedModule} from './../shared/shared.module';
import {HomeRouter} from 'app/home/home-router';

import {MatButtonModule, MatCheckboxModule, MatDialogModule, MatRadioModule} from '@angular/material';
import {SwiperModule} from 'ngx-swiper-wrapper';

// import {LoadingComponent} from './loading/loading.component';

import {OutpatientInfoComponent} from './outpatient-info/outpatient-info.component';
import {BannerComponent} from './banner/banner.component';

import { SystemWaitingComponent } from 'app/home/system-waiting/system-waiting.component';
import { DebugComponent } from './debug/debug.component';

import { SelectCardTypeComponent } from 'app/home/select-card-type/select-card-type.component';
import { ReadCardBaseComponent } from './read-card-base/read-card-base.component';
import { ReadBarcodeComponent } from 'app/home/read-barcode/read-barcode.component';
import { ReadCardM1Component } from 'app/home/read-card-m1/read-card-m1.component';
import { ReadCardMagneticComponent } from 'app/home/read-card-magnetic/read-card-magnetic.component';



@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    SharedModule,
    MatButtonModule,
    MatCheckboxModule,
    MatDialogModule,
    MatRadioModule,
    SwiperModule,
    HomeRouter
  ],
  declarations: [
    // LoadingComponent,
    OutpatientInfoComponent,
    BannerComponent,
    SystemWaitingComponent,
    DebugComponent,
    SelectCardTypeComponent,
    ReadCardBaseComponent,
    ReadBarcodeComponent,
    ReadCardM1Component,
    ReadCardMagneticComponent,
  ],
  exports: []
})
export class HomeModule {
}



