// 账单列表项

import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';

import {OutpatientPayRecordVo} from 'app/core/objects/outpatient-pay-record-vo';
import {OutputQueryOutpatientPayRecordsListItemFeeDetailItem} from 'app/protocol/server/output-query-outpatient-pay-records-list-item-fee-detail-item';
import {UserService} from 'app/core/services/user.service';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {OutputQueryOutpatientPayDetails} from 'app/protocol/server/output-query-outpatient-pay-details';
import {OutputQueryOutpatientPayDetailsListItem} from 'app/protocol/server/output-query-outpatient-pay-details-list-item';
import {LoadingService} from 'app/core/services/loading.service';
import {LogService} from 'app/core/services/log.service';
import {ErrorService} from 'app/core/services/error.service';
import {SystemService} from 'app/core/services/system.service';
import {InfoLibrary} from 'app/core/libs/info-library';
import {OutpatientPayRecordDetail} from 'app/core/objects/outpatient-pay-record-detail';
import {InfoMessage} from 'app/core/objects/info-message';
import {RouterInfo} from 'app/core/objects/router-info';
import {PathLibrary} from 'app/core/libs/path-library';

@Component({
  selector: 'app-billing-item',
  templateUrl: './billing-item.component.html',
  styleUrls: ['./billing-item.component.css']
})
export class BillingItemComponent implements OnInit, OnChanges {

  @Input()
  showCheck: boolean; // 是否显示勾选框

  @Input()
  showExpand: boolean; // 是否显示展开按钮

  @Input()
  showTimeDetail: boolean; // 是否显示时分秒

  @Input()
  itemValue: OutpatientPayRecordVo; // 账单项数据

  @Output()
  changed: EventEmitter<OutpatientPayRecordVo> = new EventEmitter<OutpatientPayRecordVo>(); // 改变事件

  isExpand: boolean; // 是否展开

  feeDate: string; // 费用产生的日期及时间
  detailVOList: Array<OutpatientPayRecordDetail> = []; // 费用明细，模板显示使用

  constructor(private userService: UserService,
              private loadingService: LoadingService,
              private errorService: ErrorService,
              private systemService: SystemService) {
  }

  ngOnInit() {
    this.isExpand = false;
  }

  ngOnChanges(changes: SimpleChanges) {

    // 构造详情列表
    this.createDetailList(this.itemValue.outpatientPayRecordsListItem.feeDetail);

    // 是否需要处理显示的日期
    if (this.showTimeDetail) {
      this.feeDate = this.itemValue.outpatientPayRecordsListItem.fee_date;
    } else {
      this.feeDate = this.itemValue.outpatientPayRecordsListItem.fee_date.split(' ')[0];
    }
  }

  // 本项被点击时
  itemClicked(): void {
    if (this.showCheck) {
      this.checkChanged(!this.itemValue.isSelect);
    }
  }

  // 右边一小块区域被点击时
  itemRightClicked($event: any): void {
    if (this.showExpand) {
      this.expandChanged(!this.isExpand);
    }

    // 阻止事件被扩散出去，不要触发其他点击行为
    $event.stopPropagation();
  }

  expandChanged(isExpand: boolean): void {
    this.isExpand = isExpand;

    if (this.detailVOList.length === 0) {
      // 进一步获取明细信息
      this.getPayDetailList();
    }
  }

  // 选中组件 点击导致的改变时触发
  checkChanged(isChecked: boolean): void {
    this.itemValue.isSelect = isChecked;
    this.changed.emit(this.itemValue);
  }

  // 从本项数据中直接提取的
  private createDetailList(itemList: Array<OutputQueryOutpatientPayRecordsListItemFeeDetailItem>): void {
    if ((itemList) && (itemList.length > 0)) {
      itemList.forEach(
        (item: OutputQueryOutpatientPayRecordsListItemFeeDetailItem) => {
          this.detailVOList.push(OutpatientPayRecordDetail.createByItemDetail(item));
        }
      );
    }
  }

  // 从额外的接口获得的数据
  private createDetailList2(itemList: Array<OutputQueryOutpatientPayDetailsListItem>): void {
    if ((itemList) && (itemList.length > 0)) {
      itemList.forEach(
        (item: OutputQueryOutpatientPayDetailsListItem) => {
          this.detailVOList.push(OutpatientPayRecordDetail.createByDetail(item));
        }
      );
    }
  }

  // 获取门诊病人缴费项的明细内容
  private getPayDetailList(): void {

    this.loadingService.showLoading();

    const outputQueryOutPatientInfo: OutputQueryOutPatientInfo = this.userService.getOutPatientInfo();
    this.userService.getOutpatientPayDetailsFromServer(
      outputQueryOutPatientInfo.patient_card,
      outputQueryOutPatientInfo.real_name,
      this.itemValue.outpatientPayRecordsListItem.his_id
    ).subscribe(
      data => this.getOutpatientPayDetailsSuc(data),
      error => this.getOutpatientPayDetailsErr(error)
    );
  }

  // 获取门诊病人缴费项的明细内容，成功
  private getOutpatientPayDetailsSuc(outputQueryOutpatientPayDetails: OutputQueryOutpatientPayDetails): void {
    LogService.debug('getOutpatientPayDetailsSuc: ' + outputQueryOutpatientPayDetails);
    this.createDetailList2(outputQueryOutpatientPayDetails.list);
    this.loadingService.hiddenLoading();
  }

  // 获取门诊病人缴费项的明细内容，失败
  private getOutpatientPayDetailsErr(errorMsg: string): void {
    LogService.error('getOutpatientPayDetailsErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(InfoLibrary.inpatientNumError);
      infoMessage.routerInfo = new RouterInfo([PathLibrary.inpatientLogin], null);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

}



