// 设备身份认证

import {Injectable} from '@angular/core';
import {Observable, Subscriber, Subscription} from 'rxjs/Rx';

import {BaseService} from 'app/core/bases/base-service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {LogService} from 'app/core/services/log.service';
import {ServerRequestService} from 'app/protocol/server/server-request.service';
import {ServerApiLibrary} from 'app/protocol/server/server-api-library';
import {InputDeviceLogin} from 'app/protocol/server/input-device-login';
import {InputDeviceInfo} from 'app/protocol/server/input-device-info';
import {ResponseDataWrap} from 'app/core/objects/response-data-wrap';
import {OutputGetConfig} from 'app/protocol/shell/output-get-config';
import {PublicLibrary} from 'app/core/libs/public-library';
import {InfoLibrary} from 'app/core/libs/info-library';

@Injectable()
export class AuthService extends BaseService {

  private getConfigFromShellSubscriber: Subscriber<boolean>;
  private getTokenFromServerSubscriber: Subscriber<boolean>;

  private web2ShellGetConfigSubscription: Subscription;

  constructor(private shellRequestService: ShellRequestService,
              private serverRequestService: ServerRequestService) {
    super();
  }

  init(): void {
  }

  // 从外壳获取配置信息
  public getConfigFromShell(): Observable<boolean> {
    LogService.debug('get config info start ...');

    const getConfigFromShellObservable: Observable<boolean> = new Observable(
      subscriber => {
        this.getConfigFromShellSubscriber = subscriber;
      }
    );

    this.web2ShellGetConfigSubscription = this.shellRequestService.web2ShellGetConfig().subscribe(
      data => this.getConfigSuc(data),
      error => this.getConfigErr(error)
    );

    return getConfigFromShellObservable;
  }

  // 获取设备配置，成功
  private getConfigSuc(outputGetConfig: OutputGetConfig) {

    // 不再接受壳中此类消息
    this.web2ShellGetConfigSubscription.unsubscribe();

    if (outputGetConfig.hospitalID && outputGetConfig.server && outputGetConfig.mode) {
      // 配置有效

      // 保存配置信息
      ServerApiLibrary.authInfo.setValue(outputGetConfig);

      this.getConfigFromShellSubscriber.next(true);

    } else {
      // 配置无效
      this.getConfigFromShellSubscriber.next(false);
    }
  }

  // 获取设备配置，失败
  private getConfigErr(errorMsg: string) {
    this.getConfigFromShellSubscriber.error(errorMsg);
  }

  // ---------------------------------

  // 去服务器进行设备身份认证
  public getTokenFromServer(): Observable<boolean> {

    const getTokenFromServerObservable: Observable<boolean> = new Observable(
      subscriber => {
        this.getTokenFromServerSubscriber = subscriber;
      }
    );

    const inputDeviceLogin: InputDeviceLogin = new InputDeviceLogin();
    inputDeviceLogin.hospital_id = ServerApiLibrary.authInfo.hospitalId;

    const inputDeviceInfo: InputDeviceInfo = new InputDeviceInfo();
    inputDeviceInfo.device_id = ServerApiLibrary.authInfo.deviceID;
    inputDeviceInfo.mac_address = ServerApiLibrary.authInfo.macAddress;
    inputDeviceInfo.os_version = 'windows';
    inputDeviceInfo.app_version = '1.0';
    inputDeviceInfo.platform_type = '';

    LogService.debug('get token start ...');
    this.serverRequestService.deviceLogin(inputDeviceLogin, inputDeviceInfo)
      .subscribe(
        data => this.getTokenFromServerSuc(data),
        error => this.getTokenFromServerErr(error)
      );

    return getTokenFromServerObservable;
  }

  // 获取 token 成功的处理
  private getTokenFromServerSuc(responseDataWrap: ResponseDataWrap): void {

    if (responseDataWrap.responseCode === PublicLibrary.httpResponseSuccess) {

      // update token
      ServerApiLibrary.authInfo.token = responseDataWrap.responseToken;
      LogService.debug('get token suc, value:' + responseDataWrap.responseToken);

      this.getTokenFromServerSubscriber.next(true);

    } else if (responseDataWrap.responseCode === PublicLibrary.httpResponseDeviceNotExists) {
      LogService.error('getTokenFromServerErr: [device not exists]');
      this.getTokenFromServerSubscriber.error(InfoLibrary.deviceNotExists);

    } else {
      this.getTokenFromServerSubscriber.error(responseDataWrap.responseMessage);
    }

  }

  // 获取 token 失败的处理
  private getTokenFromServerErr(errorMsg: string): void {
    this.getTokenFromServerSubscriber.error(errorMsg);
  }

}



