// 用户信息

import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {PayInfo} from 'app/core/objects/pay-info';

export class UserInfo {

  outputQueryOutPatientInfo: OutputQueryOutPatientInfo = null; // 门诊病人
  outputQueryInHospitalPatient: OutputQueryInHospitalPatient = null; // 住院病人

  /**
   * 判断是否存在用户信息
   * @returns {boolean}
   */
  hasPatientInfo(): boolean {
    let hasInfo = false;
    if ((this.outputQueryOutPatientInfo) || (this.outputQueryInHospitalPatient)) {
      hasInfo = true;
    }
    return hasInfo;
  }

  /**
   * 清除用户信息
   */
  clearPatientInfo() {
    this.outputQueryOutPatientInfo = null;
    this.outputQueryInHospitalPatient = null;
  }

  /**
   * 获取用户的真实姓名
   */
  getRealName(): string {
    let name: string;
    if (this.outputQueryOutPatientInfo) {
      name = this.outputQueryOutPatientInfo.real_name;
    } else if (this.outputQueryInHospitalPatient) {
      name = this.outputQueryInHospitalPatient.real_name;
    }
    return name;
  }
}



