// 系统相关

import {Injectable} from '@angular/core';
import {MatDialog} from '@angular/material';

import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {AuthService} from 'app/core/services/auth.service';
import {InfoService} from 'app/core/services/info.service';
import {LogService} from 'app/core/services/log.service';
import {TimerService} from 'app/core/services/timer.service';
import {RouterService} from 'app/core/services/router.service';
import {UserService} from 'app/core/services/user.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';

import {PathLibrary} from 'app/core/libs/path-library';
import {InfoMessage} from 'app/core/objects/info-message';
import {DeviceParameter} from 'app/core/public/device-parameter';
import {ProxyLibrary} from 'app/core/libs/proxy-library';
import {ServerApiLibrary} from 'app/protocol/server/server-api-library';
import {InfoLibrary} from 'app/core/libs/info-library';
import {ErrorLibrary} from 'app/core/libs/error-library';
import {ErrorInfo} from 'app/core/objects/error-info';

@Injectable()
export class SystemService {

  constructor(private shellRequestService: ShellRequestService,
              private authService: AuthService,
              private errorService: ErrorService,
              private loadingService: LoadingService,
              private routerService: RouterService,
              private infoService: InfoService,
              private logService: LogService,
              private timerService: TimerService,
              private userService: UserService,
              private dialog: MatDialog) {
  }

  // 初始化，系统启动时被调用
  onInit() {
    // set device parameter
    DeviceParameter.init();

    // 版本发布前应改为设备运行模式
    DeviceParameter.enableWebDebugMode(); // 脱壳运行，全模拟

    // init all service
    this.shellRequestService.init();
    this.logService.init();
    this.infoService.init();
    this.routerService.init();
    this.authService.init();
    this.errorService.init();
    this.loadingService.init();
    this.timerService.init();

    // 建立获取从 ShellRequestService 发出的记录日志请求的机制
    this.shellRequestService.initLog()
      .subscribe(
        data => this.getLogFromShellRequestServiceSuc(data),
        error => this.getLogFromShellRequestServiceErr(error)
      );
  }

  // UI 初始化完成后调用，便于控制后续所有操作能在UI中显示信息
  onInitUICompleted() {
    LogService.info('UI init completed! ');
    // 开始设备身份认证过程
    this.authService.getConfigFromShell().subscribe(
      data => this.getConfigFromShellSuc(data),
      error => this.getConfigFromShellErr(error)
    );
  }

  // 需要更新 token 时
  updateToken(showMainMenuUI: boolean) {
    this.authService.getTokenFromServer().subscribe(
      data => this.getTokenFromServerSuc(data, showMainMenuUI),
      error => this.getTokenFromServerErr(error)
    );
  }

  // 退回主菜单界面，并清理相关痕迹
  backToMainMenu() {

    // 关闭所有已打开的界面
    this.dialog.closeAll();

    // 清除用户信息
    this.userService.clearUserInfo();

    // 清除路由信息
    this.routerService.clearTrace();

    // UI跳转
    this.routerService.goTo([ProxyLibrary.getMainMenu()]);
  }

  // 跳转到错误等待界面，并清理相关痕迹
  gotoErrorWait() {

    // 关闭所有已打开的界面
    this.dialog.closeAll();

    // 清除用户信息
    this.userService.clearUserInfo();

    // 清除路由信息
    this.routerService.clearTrace();

    // UI跳转
    this.routerService.goTo([PathLibrary.errorWait]);
  }

  // ---------------------------------

  // 从壳中获取设备配置成功
  private getConfigFromShellSuc(isConfigValid: boolean): void {
    if (isConfigValid) {
      // 配置信息有效

      // 设置设备模式
      DeviceParameter.setMode(ServerApiLibrary.authInfo.mode);

      // 设置医院ID
      ProxyLibrary.setHospitalId(ServerApiLibrary.authInfo.hospitalId);

      // 获取 token
      this.updateToken(true);

    } else {
      // 无法获取相关信息时，跳转错误页面
      LogService.error('get config fail: [parameters invalid]. Show Error Page.');
      const errorInfo: ErrorInfo = new ErrorInfo();
      errorInfo.errorType = ErrorLibrary.getConfigFail;
      this.routerService.goTo([PathLibrary.errorPage, errorInfo]);
    }
  }

  // 从壳中获取设备配置失败
  private getConfigFromShellErr(errorMsg: string) {
    // 获取设备配置失败时，跳转错误页面
    LogService.error('get config fail: [' + errorMsg + ']. Show Error Page.');
    const errorInfo: ErrorInfo = new ErrorInfo();
    errorInfo.errorType = ErrorLibrary.getConfigFail;
    this.routerService.goTo([PathLibrary.errorPage, errorInfo]);
  }

  // 从服务器获取token成功
  private getTokenFromServerSuc(data: boolean, showMainMenuUI: boolean): void {
    if (showMainMenuUI) {
      this.routerService.goTo([ProxyLibrary.getMainMenu()]);
    }
  }

  // 从服务器获取token失败
  private getTokenFromServerErr(errorMsg: string) {
    if (errorMsg === InfoLibrary.deviceNotExists) {
      LogService.error('device not exists!');
      const errorInfo: ErrorInfo = new ErrorInfo();
      errorInfo.errorType = ErrorLibrary.deviceNotExist;
      this.routerService.goTo([PathLibrary.errorPage, errorInfo]);

    } else {
      LogService.error('get token fail: [' + errorMsg + ']');
      this.routerService.goTo([PathLibrary.errorWait]);

    }
  }

  // 获取从 ShellRequestService 发出的记录日志请求
  private getLogFromShellRequestServiceSuc(infoMessage: InfoMessage): void {
    if (infoMessage.status === InfoMessage.statusNormal) {
      LogService.debug(infoMessage.data);
    } else {
      LogService.error(infoMessage.data);
    }
  }

  // 获取从 ShellRequestService 发出的记录日志请求，异常
  private getLogFromShellRequestServiceErr(errorMsg: string): void {
    LogService.error('getLogFromShellRequestService fail: [' + errorMsg + ']');
  }

}



