// 读 M1 卡

import {Component, OnDestroy, OnInit} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

import {LogService} from 'app/core/services/log.service';
import {ReadCardBaseComponent} from 'app/home/read-card-base/read-card-base.component';
import {OutputReadM1Card} from 'app/protocol/shell/output-read-m1-card';

@Component({
  selector: 'app-read-card-m1',
  templateUrl: './read-card-m1.component.html',
  styleUrls: ['./read-card-m1.component.css']
})
export class ReadCardM1Component extends ReadCardBaseComponent implements OnInit, OnDestroy {

  private readonly timerInitialDelay: number = 800; // 初次开始生效的延迟时间，0.8秒钟
  private readonly timerPeriodCheckCard: number = 800; // 时间间隔，0.8秒钟

  private web2ShellGetReadCardResultSubscription: Subscription; // 从设备获取卡信息, M1
  private checkCardTimerSubscription: Subscription; // 计时器，检查卡是否读出来了
  private hasGetCardID = false;

  ngOnInit() {
    super.ngOnInit();
    this.init();
  }

  ngOnDestroy() {
    super.ngOnDestroy();
    if (this.checkCardTimerSubscription) {
      this.checkCardTimerSubscription.unsubscribe();
    }
    if (this.web2ShellGetReadCardResultSubscription) {
      this.web2ShellGetReadCardResultSubscription.unsubscribe();
    }
  }

  private init() {
    const checkCardTimerObservable: Observable<number> = TimerObservable.create(this.timerInitialDelay, this.timerPeriodCheckCard);
    this.checkCardTimerSubscription = checkCardTimerObservable.subscribe(
      t => {
        LogService.debug('try to check m1 card ...');
        // 不断尝试
        if (!this.hasGetCardID) {
          this.waitReadCardResult();
        }
      });
  }

  // 等待读卡结果
  private waitReadCardResult() {
    this.web2ShellGetReadCardResultSubscription = this.shellRequestService.web2ShellReadM1Card()
      .subscribe(
        data => this.getReadCardResultSuc(data),
        error => this.getReadCardResultErr(error)
      );
  }

  // 等待读卡结果，成功
  private getReadCardResultSuc(outputReadM1Card: OutputReadM1Card) {
    LogService.debug('get read card result suc: [' + outputReadM1Card.cardID + ']');
    if (outputReadM1Card.cardID.length > 0) {
      this.hasGetCardID = true;
      this.checkCardTimerSubscription.unsubscribe();
      this.web2ShellGetReadCardResultSubscription.unsubscribe();
      this.getCardIDHandle(outputReadM1Card.cardID);
    }
  }

  // 等待读卡结果，失败
  private getReadCardResultErr(errorMsg: string) {
    LogService.error('get read card result fail: [' + errorMsg + ']');
  }

}



