// header, 显示 用户信息、返回按钮
import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';

import {UserService} from 'app/core/services/user.service';
import {SystemService} from 'app/core/services/system.service';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {PayLibrary} from 'app/core/libs/pay-library';
import {TimerComponent} from 'app/shared/timer/timer.component';

@Component({
  selector: 'app-main-header',
  templateUrl: './main-header.component.html',
  styleUrls: ['./main-header.component.css']
})
export class MainHeaderComponent implements OnInit {

  @Input()
  infoHidden: boolean;  // 是否隐藏用户信息

  @Input()
  timerShow: boolean;  // 是否显示倒计时

  @Input()
  timerMax: number; // 倒计时上限

  @Input()
  dataSrc: string;  // 缴费类型

  @Input()
  timerHandle: boolean;  // 是否处理倒计时完成，true 时发出事件，false 时不发出事件且执行默认处理

  @Output()
  timerCompleted: EventEmitter<boolean> = new EventEmitter<boolean>(); // 定时器完成事件

  outpatientInfo: OutputQueryOutPatientInfo;
  inpatientInfo: OutputQueryInHospitalPatient;

  private timerComponent: TimerComponent; // timer 组件

  // 设置 timer 组件
  @ViewChild(TimerComponent)
  set timer(content: TimerComponent) {
    this.timerComponent = content;
  }

  constructor(private userService: UserService,
              private systemService: SystemService) {
  }

  ngOnInit() {
    if (this.dataSrc === PayLibrary.dataSrcInpatient) {
      this.inpatientInfo = this.userService.getInPatientInfo();
    } else {
      this.outpatientInfo = this.userService.getOutPatientInfo();
    }
  }

  // 对外公开的行为，重置 timer 并立即启动
  restartTimer(timerMax: number) {
    if (this.timerComponent) {
      this.timerComponent.resetAndStart(timerMax);
    }
  }

  goToReturn(): void {
    this.systemService.backToMainMenu();
  }

  // 定时器时间到
  timerCompletedHandle(event: number) {
    if (this.timerHandle) {
      // 事件传播出去
      this.timerCompleted.emit(true);

    } else {
      // 事件不传播，内部直接处理掉
      this.systemService.backToMainMenu();
    }
  }
}



