// 展开按钮

import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';

@Component({
  selector: 'app-expand-button',
  templateUrl: './expand-button.component.html',
  styleUrls: ['./expand-button.component.css']
})
export class ExpandButtonComponent implements OnInit, OnChanges {

  private static readonly iconToExpand: string = 'assets/images/arrow_down.png'; // 显示扩展
  private static readonly iconToContract: string = 'assets/images/arrow_up.png'; // 显示收缩

  @Input()
  isExpand: boolean; // 是否展开

  @Output()
  changed: EventEmitter<boolean> = new EventEmitter<boolean>(); // 改变事件

  public iconPath: string; // 图标地址

  constructor() {
  }

  ngOnInit() {
    this.updateView();
  }

  ngOnChanges(changes: SimpleChanges) {
    this.updateView();
  }

  clickHandle($event: any): void {
    this.isExpand = !this.isExpand;
    this.changed.emit(this.isExpand);

    this.updateView();

    // 停止事件扩散
    $event.stopPropagation();
  }

  private updateView(): void {
    if (this.isExpand) {
      this.iconPath = ExpandButtonComponent.iconToContract;
    } else {
      this.iconPath = ExpandButtonComponent.iconToExpand;
    }
  }

}



