// 模块内的路由设置

import {ModuleWithProviders} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {PathLibrary} from 'app/core/libs/path-library';
import {SystemWaitingComponent} from 'app/home/system-waiting/system-waiting.component';
import {BannerComponent} from 'app/home/banner/banner.component';
import {DebugComponent} from 'app/home/debug/debug.component';
import {SelectCardTypeComponent} from 'app/home/select-card-type/select-card-type.component';
import {ReadBarcodeComponent} from 'app/home/read-barcode/read-barcode.component';
import {ReadCardM1Component} from 'app/home/read-card-m1/read-card-m1.component';
import {ReadCardMagneticComponent} from 'app/home/read-card-magnetic/read-card-magnetic.component';

const routes: Routes = [
  {path: PathLibrary.debug, component: DebugComponent},
  {path: PathLibrary.banner, component: BannerComponent},
  {path: PathLibrary.systemWaiting, component: SystemWaitingComponent},
  {path: PathLibrary.selectCardType, component: SelectCardTypeComponent},
  {path: PathLibrary.readBarcode, component: ReadBarcodeComponent},
  {path: PathLibrary.readCardM1, component: ReadCardM1Component},
  {path: PathLibrary.readCardMagnetic, component: ReadCardMagneticComponent},
];

export const HomeRouter: ModuleWithProviders = RouterModule.forChild(routes);



