// 支付订单相关信息，主要用于在页面跳转的传输中传递数据

export class PayInfo {

  dataSrc: string; // 缴费类型
  totalAmount: string; // 总金额
  debt: string; // 需要补缴的金额
  visitID: string; // His批次号，data_src取值hisSrc时,此字段必填(与查询缴费记录返回的visit_id一致),其他不填

  constructor() {
    this.dataSrc = '';
    this.totalAmount = '';
    this.debt = '';
    this.visitID = '';
  }

}



