// 错误页面，用于设备无法正常提供服务，也无法继续进行其他操作的场合
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {InfoLibrary} from 'app/core/libs/info-library';
import {ErrorLibrary} from 'app/core/libs/error-library';
import {ErrorInfo} from 'app/core/objects/error-info';

@Component({
  selector: 'app-error-page',
  templateUrl: './error-page.component.html',
  styleUrls: ['./error-page.component.css']
})
export class ErrorPageComponent implements OnInit {

  tips: string;

  private errorInfo: ErrorInfo;

  constructor(private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.getParametersFromURL();
    this.updateView();
  }

  private getParametersFromURL() {
    this.route.params.subscribe(params => {
      this.errorInfo = Object.assign(new ErrorInfo(), params);
    });
  }

  private updateView() {
    if (this.errorInfo.errorType === ErrorLibrary.getConfigFail) {
      this.tips = InfoLibrary.getConfigFail;

    } else if (this.errorInfo.errorType === ErrorLibrary.deviceNotExist) {
      this.tips = InfoLibrary.deviceNoExist;

    } else if (this.errorInfo.errorType === ErrorLibrary.getTokenFail) {
      this.tips = InfoLibrary.getTokenFail;

    } else {
      this.tips = InfoLibrary.unknownError;
    }
  }

}



