// 负责代理和各医院定制模块相关的内容，对外统一名称，对内根据医院ID来决定实际的值

import {PathLibrary} from 'app/core/libs/path-library';
import {CustomConfig} from 'app/core/bases/custom-config';
import {AqsdermyyCustomConfig} from 'app/c-aqsdermyy/aqsdermyy-custom-config';
import {HnxrmyyCustomConfig} from 'app/c-hnxrmyy/hnxrmyy-custom-config';
import {SqsrmyyCustomConfig} from 'app/c-sqsrmyy/sqsrmyy-custom-config';
import {CoverItem} from 'app/core/objects/cover-item';
import {CoverItemLibrary} from 'app/core/libs/cover-item-library';

export class ProxyLibrary {

  private static theCustomConfig: CustomConfig; // 当前医院的配置信息

  private static hospitalId: string;

  /**
   * 设置 hospitalId，后续所有定制化内容都以此为基础，本方法只对内使用，用于调试时。
   * @param {string} hospitalId 医院ID
   */
  public static setHospitalId(hospitalId: string) {
    ProxyLibrary.hospitalId = hospitalId;
  }

  /**
   * 获取医院 icon 地址
   * @returns {string}
   */
  public static getIconURL(): string {
    let url = '';
    if (ProxyLibrary.getCustomConfig()) {
      url = ProxyLibrary.getCustomConfig().getIconURL();
    }
    return url;
  }

  /**
   * 获取主菜单 item 数据
   * @returns {CoverItem[]}
   */
  public static getMenuItemData(): Array<CoverItem> {
    let coverData: CoverItem[] = [];
    if (ProxyLibrary.getCustomConfig()) {
      coverData = ProxyLibrary.getCustomConfig().getMenuItemData();

    } else {
      // 没获取到配置信息，可能是开发阶段，配置开发信息
      coverData.push(new CoverItem(
        CoverItemLibrary.iconOutpatientPaymentQuery,
        'Test',
        PathLibrary.debug,
        null
      ));

    }
    return coverData;
  }

  /**
   * 获取就诊卡类型
   * @returns {string}
   */
  public static getCardType(): string {
    return ProxyLibrary.getCustomConfig().getCardType();
  }

  /**
   * 获取支持的支付类型
   * @returns {Array<string>}
   */
  public static getPayType(): Array<string> {
    return ProxyLibrary.getCustomConfig().getPayType();
  }

  /**
   * 获取门诊病人账单 item 中是否显示时分秒
   * @returns {boolean}
   */
  public static getShowTimeDetailInOutpatientBillingItem(): boolean {
    return ProxyLibrary.getCustomConfig().showTimeDetailInOutpatientBillingItem();
  }

  /**
   * 获取门诊病人缴费类型
   * @returns {string}
   */
  public static getOutpatientPayDataSrc(): string {
    return ProxyLibrary.getCustomConfig().getOutpatientPayType();
  }

  /**
   * 获取住院病人缴费类型
   * @returns {string}
   */
  public static getInpatientPayDataSrc(): string {
    return ProxyLibrary.getCustomConfig().getInpatientPayType();
  }

  /**
   * 获取主菜单状态名，供路由使用
   * @returns {string}
   */
  public static getMainMenu(): string {
    const path = PathLibrary.moduleName_Base + '/' + PathLibrary.mainMenu;
    return path;
  }

  // /**
  //  * 获取门诊缴费UI状态名，供路由使用
  //  * @returns {string}
  //  */
  // public static getOutpatientBilling(): string {
  //   const path = ProxyLibrary.getModuleNamePrefix() + PathLibrary.outpatientBilling;
  //   // console.log('--- path ---', path)
  //   // path = path.replace('/', '-');
  //   return path;
  // }

  /**
   * 获取住院缴费UI状态名，供路由使用
   * @returns {string}
   */
  public static getInpatientBilling(): string {
    const path = PathLibrary.inpatientLogin;
    return path;
  }

  /**
   * 获取一卡通充值UI状态名，供路由使用
   * @returns {string}
   */
  public static getEcardRecharge(): string {
    const path = PathLibrary.paymentAmountSelect;
    return path;
  }

  /**
   * 获取扫码支付UI状态名，供路由使用
   * @returns {string}
   */
  public static getPayment(): string {
    let path: string;
    if (ProxyLibrary.hospitalId === PathLibrary.hospitalID_AQEY) {
      path = ProxyLibrary.getModuleNamePrefix() + PathLibrary.payment;
    } else {
      path = PathLibrary.moduleName_Base + '/' + PathLibrary.payment;
    }

    return path;
  }

  /**
   * 获取扫码支付确认结果UI状态名，供路由使用
   * @returns {string}
   */
  public static getPaymentResult(): string {
    const path = ProxyLibrary.getModuleNamePrefix() + PathLibrary.paymentResult;
    return path;
  }


  // 获取定制化模块的模块名前缀
  private static getModuleNamePrefix(): string {
    let prefix = '--';
    if (ProxyLibrary.getCustomConfig()) {
      prefix = ProxyLibrary.getCustomConfig().getModuleNamePrefix();
    }
    prefix += '/';

    return prefix;
  }

  // 获取定制化模块的配置信息
  private static getCustomConfig(): CustomConfig {
    if (!ProxyLibrary.theCustomConfig) {
      switch (ProxyLibrary.hospitalId) {
        case PathLibrary.hospitalID_AQEY:
          ProxyLibrary.theCustomConfig = new AqsdermyyCustomConfig();
          break;

        case PathLibrary.hospitalID_HNRY:
          ProxyLibrary.theCustomConfig = new HnxrmyyCustomConfig();
          break;

        case PathLibrary.hospitalID_SQRY:
          ProxyLibrary.theCustomConfig = new SqsrmyyCustomConfig();
          break;

        default:
          console.error('CustomConfig not found for: ' + ProxyLibrary.hospitalId);
      }
    }

    return ProxyLibrary.theCustomConfig;
  }

}



