// 订单状态检查服务

import {Injectable} from '@angular/core';
import {Observable, Subscriber, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

import {LogService} from 'app/core/services/log.service';
import {UserService} from 'app/core/services/user.service';
import {OutputQueryOrderStatus} from 'app/protocol/server/output-query-order-status';
import {PayLibrary} from 'app/core/libs/pay-library';

@Injectable()
export class OrderCheckService {

  private readonly timerInitialDelay: number = 9000; // 初次开始生效的延迟时间，9秒
  private readonly timerPeriodCheckOrder: number = 3000; // 检查版本的时间间隔，3秒

  private tradeNo: string; // 订单号
  private orderStatusSubscriber: Subscriber<OutputQueryOrderStatus>; // 提供给外部的观察对象，订单状态
  private checkOrderSubscription: Subscription; // 计时器，检查订单状态

  constructor(private userService: UserService) {
  }

  // 订阅，内部开始读秒机制
  start(tradeNo: string): Observable<OutputQueryOrderStatus> {

    this.tradeNo = tradeNo;

    // 对外的
    const orderStatusObservable: Observable<OutputQueryOrderStatus> = new Observable(
      subscriber => {
        this.orderStatusSubscriber = subscriber;
      }
    );

    // 对内的
    const checkOrderObservable: Observable<number> = TimerObservable.create(this.timerInitialDelay, this.timerPeriodCheckOrder);
    this.checkOrderSubscription = checkOrderObservable.subscribe(
      t => {
        LogService.debug('try to check order ...');
        this.checkOrder();
      });

    return orderStatusObservable;
  }

  // 停止检查
  stop() {
    if (this.checkOrderSubscription) {
      this.checkOrderSubscription.unsubscribe();
    }
    this.tradeNo = '';
  }

  // 检查订单状态
  private checkOrder() {
    this.userService.queryOrderStatusFromServer(this.tradeNo).subscribe(
      data => this.getOrderStatusSuc(data),
      error => this.getOrderStatusErr(error)
    );
  }

  // 查询订单状态，成功
  private getOrderStatusSuc(outputQueryOrderStatus: OutputQueryOrderStatus): void {

    this.orderStatusSubscriber.next(outputQueryOrderStatus);

    if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusSucToHIS
      || outputQueryOrderStatus.order_status === PayLibrary.orderStatusFailedToHIS) {
      // 结束
      this.orderStatusSubscriber.complete();
      this.stop();
    }

  }

  // 查询订单状态，失败
  private getOrderStatusErr(errorMsg: string): void {
    this.orderStatusSubscriber.error(errorMsg);
  }

}



