// 门诊病人付费记录对象，系统内使用

import {OutputQueryOutpatientPayRecordsListItem} from 'app/protocol/server/output-query-outpatient-pay-records-list-item';

export class OutpatientPayRecordVo {
  outpatientPayRecordsListItem: OutputQueryOutpatientPayRecordsListItem; // 门诊病人付费记录信息
  isSelect: boolean; // 是否被选中

  constructor(outpatientPayRecordsListItem: OutputQueryOutpatientPayRecordsListItem,
              isSelect: boolean) {
    this.outpatientPayRecordsListItem = outpatientPayRecordsListItem;
    this.isSelect = isSelect;
  }
}



