// 公共常量的定义

export class PublicLibrary {
  public static readonly httpResponseSuccess = 'success'; // 访问服务器请求成功，正常状态
  public static readonly httpResponseTokenTimeout = 'token_timeout'; // 访问服务器请求失败，token 超时
  public static readonly httpResponseDeviceNotExists = 'device_not_exists'; // 访问服务器请求失败，设备未注册
}



