// 主菜单按钮
import {Component, Input, OnInit} from '@angular/core';

import {CoverItem} from 'app/core/objects/cover-item';
import {RouterService} from 'app/core/services/router.service';
import {NextPath} from 'app/core/objects/next-path';

@Component({
  selector: 'app-main-menu-item',
  templateUrl: './main-menu-item.component.html',
  styleUrls: ['./main-menu-item.component.css']
})

export class MainMenuItemComponent implements OnInit {

  @Input()
  itemData: CoverItem;

  constructor(private routerService: RouterService) {
  }

  ngOnInit() {
  }

  goToDetail(): void {
    if (!this.itemData.nextPath) {
      this.routerService.goTo([this.itemData.path]);

    } else {
      const nextPath: NextPath = new NextPath();
      nextPath.next = this.itemData.nextPath;

      this.routerService.goTo([this.itemData.path, nextPath]);
    }
  }

}



