// 主菜单上菜单项数据格式定义

export class CoverItem {
  imageURL: string; // 显示的图片
  title: string; // 显示的文字
  path: string; // 路由地址
  nextPath: string; // 下一步的路由地址，在指定跳转到后续地址时才使用，比如读卡后显示跳转某个指定 state

  constructor(imageURL: string,
              title: string,
              path: string,
              nextPath: string) {
    this.imageURL = imageURL;
    this.title = title;
    this.path = path;
    this.nextPath = nextPath;
  }

}



