// 支付相关常量定义

export class PayLibrary {
  public static readonly dataSrcHIS = 'hisSrc'; // 缴费类型：门诊缴费
  public static readonly dataSrcInpatient = 'prepaySrc'; // 缴费类型：住院预缴
  public static readonly dataSrcRegister = 'register';        // 缴费类型：挂号缴费
  public static readonly dataSrcSubscription = 'subscription';        // 缴费类型：预约缴费
  public static readonly dataSrcOneCardPay = 'eCardPay';         // 缴费类型：一卡通充值

  public static readonly payTypeWeChatQRCode = 'wechatQrcode';   // 支付渠道：微信扫码支付
  public static readonly payTypeALiPayQRCode = 'alipayQrcode';   // 支付渠道：支付宝扫码支付

  public static readonly orderStatusWaitPay = '1';               // 待支付（生成预订单后）
  public static readonly orderStatusNotStartToHIS = '2';         // 已支付正在通知HIS中（第三方平台扣费成功后）
  public static readonly orderStatusPaying = '6';                // 正在支付（统一支付渠道下单后）
  public static readonly orderStatusFailedToHIS = '7';           // 支付成功通知HIS失败（HIS应答不成功后）
  public static readonly orderStatusSucToHIS = '9';              // 支付成功通知HIS成功（HIS应答成功后）

  public static readonly PaySuccess = 'success';                 // 支付结果：支付成功通知HIS成功
  public static readonly PayFail = 'fail';                       // 支付结果：支付失败
  public static readonly FailedToHIS = 'HISFail';                // 支付结果：支付失败通知HIS失败
}



