// 选择读卡的类型，用于用户的身份认证

import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {NextPath} from 'app/core/objects/next-path';
import {PathLibrary} from 'app/core/libs/path-library';
import {RouterService} from 'app/core/services/router.service';

@Component({
  selector: 'app-select-card-type',
  templateUrl: './select-card-type.component.html',
  styleUrls: ['./select-card-type.component.css']
})
export class SelectCardTypeComponent implements OnInit {

  private nextPath: NextPath; // 下一个 state

  constructor(private activatedRoute: ActivatedRoute,
              private routerService: RouterService) {
  }

  ngOnInit() {
    this.getParameterFromRoute();
  }

  // 读磁条卡
  readCardMagnetic(): void {
    this.routerService.goTo([PathLibrary.readCardMagnetic, this.nextPath]);
  }

  // 读M1卡
  readCardM1(): void {
    this.routerService.goTo([PathLibrary.readCardM1, this.nextPath]);
  }

  // 读条形码
  readBarcode(): void {
    this.routerService.goTo([PathLibrary.readBarcode, this.nextPath]);
  }

  // 解析路由参数，获取下一步要去的位置
  private getParameterFromRoute() {
    this.activatedRoute.params.subscribe(params => {
      this.nextPath = Object.assign(new NextPath(), params);
    });
  }

}



