// 多选框

import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';

@Component({
  selector: 'app-check-box',
  templateUrl: './check-box.component.html',
  styleUrls: ['./check-box.component.css']
})
export class CheckBoxComponent implements OnInit, OnChanges {

  private static readonly iconChecked: string = 'assets/images/icon_check_s.png'; // 选中
  private static readonly iconUnchecked: string = 'assets/images/icon_check_n.png'; // 未选中

  @Input()
  isChecked: boolean; // 是否选中

  @Output()
  changed: EventEmitter<boolean> = new EventEmitter<boolean>(); // 改变事件

  public iconPath: string; // 图标地址

  constructor() {
  }

  ngOnInit() {
    this.updateView();
  }

  ngOnChanges(changes: SimpleChanges) {
    this.updateView();
  }

  clickHandle($event: any): void {
    this.isChecked = !this.isChecked;
    this.changed.emit(this.isChecked);

    this.updateView();

    // 停止事件扩散
    $event.stopPropagation();
  }

  private updateView(): void {
    if (this.isChecked) {
      this.iconPath = CheckBoxComponent.iconChecked;
    } else {
      this.iconPath = CheckBoxComponent.iconUnchecked;
    }
  }

}



