import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {PathLibrary} from 'app/core/libs/path-library';
import {OutpatientPaymentRecordComponent} from './outpatient/outpatient-payment-record/outpatient-payment-record.component';
import {PaymentAmountSelectComponent} from './shared/payment-amount-select/payment-amount-select.component';
import {PaymentTypeSelectComponent} from './shared/payment-type-select/payment-type-select.component';
import {PaymentComponent} from './shared/payment/payment.component';
import {PaymentResultComponent} from './shared/payment-result/payment-result.component';
import {InpatientLoginComponent} from './inpatient/inpatient-login/inpatient-login.component';
import {ErrorPageComponent} from './shared/error-page/error-page.component';
import {ErrorWaitComponent} from './shared/error-wait/error-wait.component';

const routes: Routes = [
  {path: PathLibrary.index, redirectTo: PathLibrary.systemWaiting, pathMatch: 'full'},

  {path: PathLibrary.payment, component: PaymentComponent},
  {path: PathLibrary.paymentResult, component: PaymentResultComponent},

  {path: PathLibrary.outpatientPaymentRecord, component: OutpatientPaymentRecordComponent},
  {path: PathLibrary.paymentAmountSelect, component: PaymentAmountSelectComponent},
  {path: PathLibrary.paymentTypeSelect, component: PaymentTypeSelectComponent},
  {path: PathLibrary.inpatientLogin, component: InpatientLoginComponent},
  {path: PathLibrary.errorPage, component: ErrorPageComponent},
  {path: PathLibrary.errorWait, component: ErrorWaitComponent},

  {path: PathLibrary.moduleName_Home, loadChildren: PathLibrary.moduleFullPath_Home},
  {path: PathLibrary.moduleName_Base, loadChildren: PathLibrary.moduleFullPath_Base},
  {path: PathLibrary.moduleName_AQEY, loadChildren: PathLibrary.moduleFullPath_AQEY},
  {path: PathLibrary.moduleName_HNRY, loadChildren: PathLibrary.moduleFullPath_HNRY},
  {path: PathLibrary.moduleName_SQRY, loadChildren: PathLibrary.moduleFullPath_SQRY}
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      routes,
      {
        enableTracing: false,
        useHash: true
      }
    )
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {
}



