// 出错后等待重新尝试的UI

import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

import {SystemService} from 'app/core/services/system.service';
import {LogService} from 'app/core/services/log.service';
import {TimerComponent} from 'app/shared/timer/timer.component';

@Component({
  selector: 'app-error-wait',
  templateUrl: './error-wait.component.html',
  styleUrls: ['./error-wait.component.css']
})
export class ErrorWaitComponent implements OnInit, OnDestroy {

  private readonly timerInitialDelay: number = 1500; // 初次自动重连的时间，1500ms
  private readonly timerPeriod: number = 600000; // 自动重连的时间间隔，10分钟

  btnTryDisabled: boolean; // 重试 按钮是否被禁用

  private timerSubscription: Subscription; // 计时器，内容使用
  private timerComponent: TimerComponent; // timer 组件

  // 设置 timer 组件
  @ViewChild('timer')
  set timer(content: TimerComponent) {
    this.timerComponent = content;
    if (this.timerComponent) {
      // 一旦赋值了，说明该组件被显示了，则启动它
      this.timerComponent.restart();
    }
  }

  constructor(private systemService: SystemService) {
  }

  ngOnInit() {
    this.initTimeout();
  }

  ngOnDestroy() {
    if (this.timerSubscription) {
      this.timerSubscription.unsubscribe();
    }
  }

  // 重新试着获取 token
  tryAgain() {
    this.btnTryDisabled = true;
    this.systemService.updateToken(true);
  }

  // 定时器时间到
  timerCompleted() {
    this.btnTryDisabled = false;
  }

  initTimeout() {
    const timerObservable: Observable<number> = TimerObservable.create(this.timerInitialDelay, this.timerPeriod);
    this.timerSubscription = timerObservable.subscribe(
      t => {
        LogService.debug('try link server auto ...');
        this.systemService.updateToken(true);
      });
  }

}



