// 扫描二维码支付页面
import {Component, OnInit, OnDestroy} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Observable, Subscriber, Subscription} from 'rxjs/Rx';

import {LogService} from 'app/core/services/log.service';
import {RouterService} from 'app/core/services/router.service';

import {UserService} from 'app/core/services/user.service';
import {OutputQueryOrderStatus} from 'app/protocol/server/output-query-order-status';
import {PayLibrary} from 'app/core/libs/pay-library';
import {ProxyLibrary} from 'app/core/libs/proxy-library';
import {OutputQueryOutPatientInfo} from 'app/protocol/server/output-query-out-patient-info';
import {OutputQueryInHospitalPatient} from 'app/protocol/server/output-query-in-hospital-patient';
import {InfoLibrary} from 'app/core/libs/info-library';
import {SystemService} from 'app/core/services/system.service';
import {OrderCheckService} from 'app/core/services/order-check.service';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit, OnDestroy {

  showQRCode: boolean; // 是否显示二维码UI
  showWaiting: boolean; // 是否显示等待UI

  outpatientInfo: OutputQueryOutPatientInfo;
  inpatientInfo: OutputQueryInHospitalPatient;
  paymentType: string;
  totalAmount: string;
  payUrl: string;
  tradeNo: string;
  dataSrc: string;

  queryCount = 16;

  private orderStatusSubscription: Subscription;

  constructor(private route: ActivatedRoute,
              private routerService: RouterService,
              private systemService: SystemService,
              private userService: UserService,
              private orderCheckService: OrderCheckService) {
  }

  ngOnInit() {
    console.log('---------11111111111111111111111111-------------');

    this.route.params.subscribe(params => {
      this.paymentType = params['paymentType'];
      this.totalAmount = params['totalAmount'];
      this.payUrl = params['payUrl'];
      this.tradeNo = params['tradeNo'];
      this.dataSrc = params['dataSrc'];

      if (this.dataSrc === PayLibrary.dataSrcInpatient) {
        this.inpatientInfo = this.userService.getInPatientInfo();
      } else {
        this.outpatientInfo = this.userService.getOutPatientInfo();
      }
    });

    this.showQRCodeUI();

    this.startCheckOrder(this.tradeNo);
  }

  ngOnDestroy() {
    this.orderCheckService.stop();
    if (this.orderStatusSubscription) {
      this.orderStatusSubscription.unsubscribe();
    }
  }

  private startCheckOrder(tradeNo: string): void {
    this.orderStatusSubscription = this.orderCheckService
      .start(tradeNo)
      .subscribe(
      data => this.getOrderStatusSuc(data),
      error => this.getOrderStatusErr(error)
    );
  }

  // 查询订单状态，成功
  private getOrderStatusSuc(outputQueryOrderStatus: OutputQueryOrderStatus): void {
    // LogService.debug('getOrderStatusSuc: ' + outputQueryOrderStatus);
    console.log('---------223----------------- outputQueryOrderStatus ----', outputQueryOrderStatus);

    if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusPaying) {
      // 显示二维码UI
      this.showQRCodeUI();

    } else if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusNotStartToHIS) {
      // 显示 waiting UI
      this.showWaitingUI();

    } else if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusSucToHIS) {
      // 扣费成功
      this.showResultSuccess(outputQueryOrderStatus);

    } else if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusFailedToHIS) {
      // 扣费失败
      this.showResultFail();

    } else if (outputQueryOrderStatus.order_status === PayLibrary.orderStatusWaitPay) {
      // 统一支付渠道下单 未下单，出错处理
    }

  }

  // 查询订单状态，失败
  private getOrderStatusErr(errorMsg: string): void {
    LogService.error('getOrderStatusErr: ' + errorMsg);
    if (errorMsg !== InfoLibrary.networkError) {
      this.showResultFail();
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

  // 显示二维码UI
  private showQRCodeUI(): void {
    this.showQRCode = true;
    this.showWaiting = false;
  }

  // 显示 waiting UI
  private showWaitingUI(): void {
    this.showQRCode = true;
    this.showWaiting = false;
  }

  // 显示结果，支付成功
  private showResultSuccess(outputQueryOrderStatus: OutputQueryOrderStatus): void {
    const paymentObj = {
      orderAmount: outputQueryOrderStatus.order_amount,
      orderStatus: outputQueryOrderStatus.order_status,
      paymentType: this.paymentType,
      dataSrc: this.dataSrc
    };

    this.routerService.goTo([ProxyLibrary.getPaymentResult(), paymentObj]);
  }

  // 显示结果，支付失败
  private showResultFail(): void {
    const paymentObj = {
      orderStatus: 'error',
      paymentType: this.paymentType
    };

    this.routerService.goTo([ProxyLibrary.getPaymentResult(), paymentObj]);
  }

}



