// 账单页的下部条状内容

import {Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges} from '@angular/core';

@Component({
  selector: 'app-billing-bar',
  templateUrl: './billing-bar.component.html',
  styleUrls: ['./billing-bar.component.css']
})
export class BillingBarComponent implements OnInit, OnChanges {

  @Input()
  showCheck: boolean; // 是否显示全选框

  @Input()
  showMoney: boolean; // 是否显示支付金额

  @Input()
  showBtnPay: boolean; // 是否显示支付按钮

  @Input()
  btnTitle: string; // 按钮上显示的名字

  @Input()
  total: number; // 总金额

  @Input()
  isSelectAll: boolean; // 是否选中全部

  @Output()
  changed: EventEmitter<boolean> = new EventEmitter<boolean>(); // 改变事件

  @Output()
  clicked: EventEmitter<boolean> = new EventEmitter<boolean>(); // 按钮被点击事件

  constructor() {
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
  }

  btnPayClickHandle(): void {
    this.clicked.emit(true);
  }

  // 选中按钮被点击时触发
  checkChanged(isChecked: boolean): void {
    this.isSelectAll = isChecked;

    this.changed.emit(this.isSelectAll);
  }

}



