// 倒计时读秒

import {Component, EventEmitter, Input, OnDestroy, OnInit, Output} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';

import {TimerService} from 'app/core/services/timer.service';
import {LogService} from 'app/core/services/log.service';

@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.css']
})
export class TimerComponent implements OnInit, OnDestroy {

  @Input()
  timerMax: number; // 倒计时上限

  @Input()
  start: boolean; // 是否立即开始，不立即开始就要手动调用 restart();

  @Output()
  completed: EventEmitter<number> = new EventEmitter<number>(); // 完成事件

  timerValue: string; // 读秒值，用于模板显示

  private isDoing = false; // 是否正在倒计时中
  private timerSubscription: Subscription;

  constructor(private timerService: TimerService) {
  }

  ngOnInit() {
    if (this.start) {
      this.timerStart();
    }
  }

  ngOnDestroy() {
    this.timerStop();
  }

  // 公开的行为，用于重置或手动开始
  restart() {
    this.timerStart();
  }

  // 公开的行为，用于重置倒计时上限并重新开始
  resetAndStart(timerMax: number) {
    this.timerMax = timerMax;
    this.restart();
  }

  private timerStart(): void {
    this.isDoing = true;

    const timerObservable: Observable<number> = this.timerService.start(this.timerMax);
    this.timerSubscription = timerObservable.subscribe(
      (value: number) => {
        this.timerValue = value + 's'
      },
      error => {
        LogService.debug('timer is error!');
      },
      () => {
        LogService.debug('timer is end!');
        this.isDoing = false;
        this.completed.emit(this.timerMax);
      }
    );
  }

  private timerStop(): void {
    this.timerService.stop();

    if (this.timerSubscription) {
      this.timerSubscription.unsubscribe();
    }
  }

}



