// 支付类型选择页面
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {LogService} from 'app/core/services/log.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';
import {RouterService} from 'app/core/services/router.service';
import {UserService} from 'app/core/services/user.service';
import {OutputCreateOrder} from 'app/protocol/server/output-create-order';
import {OutputUnifiedOrder} from 'app/protocol/server/output-unified-order';
import {PayLibrary} from 'app/core/libs/pay-library';
import {ProxyLibrary} from 'app/core/libs/proxy-library';
import {InfoMessage} from 'app/core/objects/info-message';
import {InfoLibrary} from 'app/core/libs/info-library';
import {SystemService} from 'app/core/services/system.service';
import {PayInfo} from 'app/core/objects/pay-info';

@Component({
  selector: 'app-payment-type-select',
  templateUrl: './payment-type-select.component.html',
  styleUrls: ['./payment-type-select.component.css']
})
export class PaymentTypeSelectComponent implements OnInit {

  private paymentType: string; // 选择的支付类型
  private payInfo: PayInfo;

  tips = InfoLibrary.tipsPaymentSelectNone; // 提示文字
  enableWeChat: boolean; // 是否支持微信支付
  enableAliPay: boolean; // 是否支持支付宝

  constructor(private route: ActivatedRoute,
              private routerService: RouterService,
              private userService: UserService,
              private systemService: SystemService,
              private errorService: ErrorService,
              private loadingService: LoadingService) {
  }

  ngOnInit() {
    this.getParametersFromURL();
    this.initUI();
  }

  // 支付类型选择之后触发
  paymentTypeClickHandle(paymentType): void {
    this.paymentType = paymentType;

    let payType: string;
    if (paymentType === 'WeChat') {
      payType = PayLibrary.payTypeWeChatQRCode;
    } else if (paymentType === 'AliPay') {
      payType = PayLibrary.payTypeALiPayQRCode;
    }

    this.createOrder(
      this.payInfo.totalAmount,
      this.payInfo.dataSrc,
      this.payInfo.visitID,
      payType);
  }

  private getParametersFromURL() {
    this.route.params.subscribe(params => {
      this.payInfo = Object.assign(new PayInfo(), params);
    });
  }

  private initUI() {
    const payTypeList: Array<string> = ProxyLibrary.getPayType();
    if (payTypeList && (payTypeList.length > 0)) {

      // 先每种方式都设置
      payTypeList.forEach((payType) => {
        if (payType === PayLibrary.payTypeWeChatQRCode) {
          this.enableWeChat = true;
          this.tips = InfoLibrary.tipsPaymentSelectWeChat;
        }
        if (payType === PayLibrary.payTypeALiPayQRCode) {
          this.enableAliPay = true;
          this.tips = InfoLibrary.tipsPaymentSelectAliPay;
        }
      });

      // 大于一种支付方式
      if (payTypeList.length > 1) {
        this.tips = InfoLibrary.tipsPaymentSelectType;
      }

    } else {
      // 无
      this.tips = InfoLibrary.tipsPaymentSelectNone;

    }
  }

  // 生成预订单
  private createOrder(totalAmt: string, dataSrc: string, visitID: string, payType: string): void {
    console.log('------ totalAmt', totalAmt);
    this.loadingService.showLoading();

    this.userService.createOrderByServer(totalAmt, dataSrc, payType, visitID).subscribe(
      data => this.createOrderSuc(data),
      error => this.createOrderErr(error)
    );
  }

  // 生成预订单，成功
  private createOrderSuc(outputCreateOrder: OutputCreateOrder): void {
    LogService.debug('createOrderSuc: ' + outputCreateOrder);

    const id = outputCreateOrder.id;
    const trade_no = outputCreateOrder.trade_no;
    const order_amount = outputCreateOrder.order_amount;

    // 统一支付渠道下单
    this.unifiedOrder(id, trade_no, order_amount);
  }

  // 生成预订单，失败
  private createOrderErr(errorMsg: string): void {
    LogService.error('createOrderErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(errorMsg);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }

  // 统一支付渠道下单
  private unifiedOrder(id: string, tradeNo: string, orderAmount: string): void {
    this.userService.unifiedOrderByServer(id, tradeNo, orderAmount).subscribe(
      data => this.unifiedOrderSuc(data),
      error => this.unifiedOrderErr(error)
    );
  }

  // 统一支付渠道下单，成功
  private unifiedOrderSuc(outputUnifiedOrder: OutputUnifiedOrder): void {
    LogService.debug('unifiedOrderSuc: ' + outputUnifiedOrder);

    const paymentObj = {
      payUrl: outputUnifiedOrder.pay_url,
      tradeNo: outputUnifiedOrder.trade_no,
      paymentType: this.paymentType,
      totalAmount: this.payInfo.totalAmount,
      dataSrc: this.payInfo.dataSrc
    };
    this.routerService.goTo([ProxyLibrary.getPayment(), paymentObj]);

    this.loadingService.hiddenLoading();
  }

  // 统一支付渠道下单，失败
  private unifiedOrderErr(errorMsg: string): void {
    LogService.error('unifiedOrderErr: ' + errorMsg);
    this.loadingService.hiddenLoading();

    if (errorMsg !== InfoLibrary.networkError) {
      // 普通错误
      const infoMessage: InfoMessage = new InfoMessage(errorMsg);
      this.errorService.showError(infoMessage);
    } else {
      // 网络错误，跳去指定UI
      this.systemService.gotoErrorWait();
    }
  }
}



