// 读条形码

import {Component, OnDestroy, OnInit} from '@angular/core';
import {Subscription} from 'rxjs/Rx';

import {LogService} from 'app/core/services/log.service';
import {ReadCardBaseComponent} from 'app/home/read-card-base/read-card-base.component';
import {OutputReadM1Card} from 'app/protocol/shell/output-read-m1-card';

@Component({
  selector: 'app-read-barcode',
  templateUrl: './read-barcode.component.html',
  styleUrls: ['./read-barcode.component.css']
})
export class ReadBarcodeComponent extends ReadCardBaseComponent implements OnInit, OnDestroy {

  private web2ShellGetReadCardResultSubscription: Subscription; // 从设备获取卡信息, 条形码

  ngOnInit() {
    super.ngOnInit();
    this.testReadBarcode();
  }

  ngOnDestroy() {
    super.ngOnDestroy();
    this.shellRequestService.web2ShellEndReadBarcode();
  }


  // 读条形码
  testReadBarcode() {
    this.web2ShellGetReadCardResultSubscription = this.shellRequestService.web2ShellReadBarcode()
      .subscribe(
        data => this.getReadCardResultSuc(data),
        error => this.getReadCardResultErr(error)
      );
  }

  // 等待读卡结果，成功
  private getReadCardResultSuc(outputReadM1Card: OutputReadM1Card) {
    // alert('条形码： ' + outputReadM1Card.cardID);
    LogService.debug('get read card result suc: [' + outputReadM1Card.cardID + ']');
    if (outputReadM1Card.cardID.length > 0) {
      this.shellRequestService.web2ShellEndReadBarcode();
      this.web2ShellGetReadCardResultSubscription.unsubscribe();

      this.getCardIDHandle(outputReadM1Card.cardID);
    }
  }

  // 等待读卡结果，失败
  private getReadCardResultErr(errorMsg: string) {
    LogService.error('get read card result fail: [' + errorMsg + ']');
  }

}



