// 门诊病人付费记录的详细内容，为便于显示使用

import {OutputQueryOutpatientPayRecordsListItemFeeDetailItem} from 'app/protocol/server/output-query-outpatient-pay-records-list-item-fee-detail-item';
import {OutputQueryOutpatientPayDetailsListItem} from 'app/protocol/server/output-query-outpatient-pay-details-list-item';

export class OutpatientPayRecordDetail {

  itemName: string; // 名称
  price: string; // 单价
  amount: string; // 数量
  charges: string; // 总价

  static createByDetail(item: OutputQueryOutpatientPayDetailsListItem) {
    const detail: OutpatientPayRecordDetail = new OutpatientPayRecordDetail();
    detail.itemName = item.item_name;
    detail.price = item.price;
    detail.amount = item.amount;
    detail.charges = item.charges;

    return detail;
  }

  static createByItemDetail(item: OutputQueryOutpatientPayRecordsListItemFeeDetailItem) {
    const detail: OutpatientPayRecordDetail = new OutpatientPayRecordDetail();
    detail.itemName = item.item_name;
    detail.price = item.price;
    detail.amount = item.amount;
    detail.charges = item.charges;

    return detail;
  }


}



