// 等待UI

import { Component, OnInit, Input } from '@angular/core';

import {WaitLibrary} from 'app/core/libs/wait-library';
import {InfoLibrary} from 'app/core/libs/info-library';

@Component({
  selector: 'app-waiting',
  templateUrl: './waiting.component.html',
  styleUrls: ['./waiting.component.css']
})
export class WaitingComponent implements OnInit {

  @Input()
  type: number; // 类型

  tips: string;

  constructor() { }

  ngOnInit() {
    this.updateView();
  }

  private updateView(): void {
    if (this.type === WaitLibrary.bootUp) {
      this.tips = InfoLibrary.waitForBootUp;

    } else if (this.type === WaitLibrary.payment) {
      this.tips = InfoLibrary.waitForPayment;

    } else {
      this.tips = '--';
    }

  }

}



